import React,{Component} from 'react';
import AppHeader  from '../appHeader';
import SearchPanel from '../searchPanel';
import PostStatusFilter from '../postStatusFilter';
import PostAddForm from '../postAddForm/';
import PostList from '../postList/';


export default class App extends Component{

    constructor(props){
        super(props);
        this.state = {
            data : [
                {label: "First post", important:false,liked:false, id:'lol'},
                {label: "Second Post", important:false,liked:false, id:'lol1'},
                {label: "Third Post", important:false,liked:false, id:'lol2'}
            ],
            term: '',
            filter: 'all'
        }

        this.maxId = 3;
    }

    deleteItem = (id) => {

        this.setState(({data}) => {
            const index = data.findIndex(elem => elem.id === id);

            //Order of before and after is essential, because else it will delete unnecessary items
            const after = data.splice(index+1,data.length);
            const before = data.splice(0,index);


            const newArr = [...before, ...after];

            return {
                data: newArr
            };
        });
    }

    addItem = (body) => {

        const newItem = {
            label: body,
            important: false,
            id: `lol${this.maxId++}`
        }

        this.setState(({data}) => {

            const newArr = [...data, newItem];

            return {
                data: newArr
            }
        });
    }

    likeItem = (id) => {
        this.setState(({data}) => {

            const index = data.findIndex(elem => elem.id === id);

            const oldItem = data[index];
            const newItem = {...oldItem, liked: !oldItem.liked}

            const newArr = [...data.slice(0,index), newItem, ...data.slice(index+1)];

            return {
                data: newArr
            }

        });
    }


    importantItem = (id) => {
        this.setState(({data}) => {

            const index = data.findIndex(elem => elem.id === id);

            const oldItem = data[index];
            const newItem = {...oldItem, important: !oldItem.important}

            const newArr = [...data.slice(0,index), newItem, ...data.slice(index+1)];

            return {
                data: newArr
            }

        });
    }

    searchPost = (items, term) => {
        if(term.length === 0){
            return items;
        }
        return items.filter((item) => {
            return item.label.indexOf(term) > -1;
        });
    }

    filterPost = (items,filter) => {
        if(filter === 'liked'){
            return items.filter((item) => item.liked);
        } else {
            return items;
        }
    }

    onUpdateSearch = (term) => {
        this.setState({term});
    }

    onFilterSelect = (filter) => {
        this.setState({filter});
    }

    render(){

        const {data,term,filter} = this.state;

        const entries = data.length;
        const liked = data.filter(item => item.liked).length;

        const visiblePosts = this.filterPost(this.searchPost(data, term), filter);

        return (
            <div className="app">
            <AppHeader 
                entries={entries}
                liked={liked}    
            />
                <div className="search-panel d-flex">
                    <SearchPanel
                        onUpdateSearch={this.onUpdateSearch}
                    />
                    <PostStatusFilter
                        filter={filter}
                        onFilterSelect={this.onFilterSelect}
                    />
                </div> 
                
                    <PostList 
                        posts={visiblePosts}
                        onDelete={this.deleteItem}
                        onLike={this.likeItem}
                        onImportant={this.importantItem}
                    />
                
                
                    <PostAddForm
                        onAdd={this.addItem}
                    />
                
            </div>
        )
    }
}
